    width: 600px;
    height: 400px;
  }

  .main-background-text {
    font-size: 11rem;
    height: 100%;
    align-items: flex-end;
  }

  .background-text {
    position: absolute;
    bottom: 200px;
    width: 100%;
    height: auto;
    text-align: center;
  }

  .show-ipad {
    display: inline-block;
  }

  .show-ipad-background-text {
    position: absolute;
    top: 200px;
  }

  .hidden {
    display: none;
  }

  .show-mobile {
    display: inline-block;
    height: 100px;
    width: 90%;
  }
}

