  }

  .main-content-h1 {
    font-size: 2rem;
    width: 100%;
  }

  .main-background-text {
    display: none;
  }

  .background-text {
    display: none;
  }

  .show-ipad-background-text {
    display: none;
  }

  .show-mobile {
    height: 50px;
  }

  .main-content-images img {
