    font-size: 3rem;
  }

  .show-ipad-background-text {
    position: absolute;
    top: 100px;
  }
}

.star {
    position: absolute;
    background-color: transparent;
    box-shadow: 0px 0px 20px #fff;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    opacity: 0; /* Chrome, Safari 5 */  /* Firefox 5-15 */ /* Opera 12+ */
    animation: twinkle infinite ease-in-out; /* Chrome, Firefox 16+, IE 10+, Safari 5 */
}

.star1 {
    top: 30%;
    left: 50%;
    animation-duration: 2s;
}

.star2 {
        top: 20%;
        left: 80%;
        animation-duration: 1.5s;
}

.star3 {
    animation-delay: 1s;
    top: 80%;
    right: 80%;
    animation-duration: 1s;
}

.star4 {
    top: 80%;
    right: 30%;
    animation-duration: 2.5s;
}

.star5 {
    top: 15%;
    right: 90%;
    height: 5px;
    width: 5px;
    animation-duration: 3s;
}

@keyframes twinkle {
    0% {
        opacity: 0;
        transform: scale(0.95);
        background-color: rgba(255,255,255,0.2);
    }

    50% {
        opacity: 1;
        transform: scale(1.1);
        background-color: rgba(255,255,255,1);
    }
    100% {
        opacity: 0;
        transform: scale(0.95);
        background-color: rgba(255,255,255,0.2);
    }
}
.nav {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: fixed;
  color: #fff;
  z-index: 4;
}

.background {
  background: #1b1b1b;
  box-shadow: 0 0 20px #000;
}

.nav-logo {
  height: 100px;
  object-fit: contain;
}

.nav-links {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
}

.nav-link {
  list-style: none;
  padding: 1rem;
  font-size: 1.5rem;
  position: relative;
  cursor: pointer;
}

.nav-link a::after {
  content: "";
  position: absolute;
  height: 5px;
  width: 0%;
  bottom: -3px;
  left: 0;
  background: #501594;
  transition: width 0.2s ease-in-out;
}

.nav-link a {
  color: #fff;
  text-decoration: none;
}

.nav-link a:hover::after {
  width: 100%;
  transition: width 0.2s ease-in-out;
}

.burger {
  display: none;
}

.burger div {
  height: 3px;
  width: 25px;
  margin: 5px;
  background: #fff;
  transition: transform 0.2s ease-in-out;
}

.line-1.toggle {
  transform: rotate(-45deg)
    translate(-5px, 6px);
}

.line-2.toggle {
  opacity: 0;
}

.line-3.toggle {
  transform: rotate(45deg)
    translate(-5px, -6px);
}

/***** Media Queires *****/
@media screen and (max-width: 1290px) {
  .nav-links {
    width: 70%;
  }
}

@media screen and (max-width: 770px) {
  .nav {
    position: relative;
    background: #1b1b1b;
  }

  .burger {
    display: inline-block;
  }
  .nav-links {
    position: absolute;
    height: 70vh;
    top: 10vh;
    width: 100%;
    flex-direction: column;
    background: #1b1b1b;
    box-shadow: 0 10px 20px #000;
    transform: scale(0);
    transition: transform 0.2s
      ease-in-out;
  }

  .nav-links.active {
    transform: scale(1);
    transition: transform 0.2s
      ease-in-out;
  }

  .nav-link {
    font-size: 2rem;
  }
}

@media screen and (max-width: 540px) {
  .nav-logo {
    height: 60px;
  }
}

@media screen and (max-width: 320px) {
  .nav-link {
    font-size: 1.5rem;
  }
}

.aboutme {
    height: 110vh;
    background: #1b1b1b;
    display: flex;
    align-items: flex-end;
    justify-content: space-evenly;
    color: #fff;
    position: relative;
    overflow-y: hidden;
}

.aboutme-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100vh;
}

.aboutme-titles {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.aboutme-title {
    font-size: 5rem;
    color: #742fc2;
    z-index: 2;
    font-weight: 900;
    margin-top: 1rem;
}

.aboutme-title-back {
    position: absolute;
    font-size: 10rem;
    color: #242424;
}

.aboutme-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    font-size: 2rem;
    height: 70%;
    text-align: center;
