.projects {
    height: 180vh;
    background: #1b1b1b;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    color: #fff;
}

.projects-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 170vh;
    width: 100%;
}

.projects-info {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 3rem;
}

.project-container {
    border: 2px solid #242424;
    height: 600px;
    width: 900px;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    justify-items: center;
    margin: 1rem;
    z-index: 2;
}

.project-container:hover {
    box-shadow: 0 0 20px #242424;
}

.project-container img {
    height: 350px;
    object-fit: contain;
    border-radius: 10px;
}

.project-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 1rem;
}

.project-title {
    font-size: 2rem;
    color: #742fc2;
    font-weight: 700;
}

.project-info {
    font-size: 1.5rem;
    padding: 0 1rem;
}

.project-features {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 1.5rem;
    margin-top: 1rem;
}

.features-header {
    color: #742fc2;
    font-weight: 700;
}

/***** Media Queires *****/
@media screen and (max-width: 1290px) {
    .projects {
        height: 200vh;
    }

    .projects-content {
        height: 190vh;
    }

    .project-container {
        height: 600px;
        width: 600px;
        grid-template-rows: 1fr 1fr 2fr;
    }

    
    .project-container img {
        height: 290px;
    }
}

@media screen and (max-width: 1025px) {
    .projects {
        height: 218vh;
    }

    .projects-content {
        height: 205vh;
    }

    .projects-info {
        flex-direction: column;
        width: 100%;
    }

    .project-container {
        height: 600px;
        width: 90%;
        grid-template-rows: 1fr 1fr 2fr;
    }

    .project-container:nth-child(1), .project-container:nth-child(3) {
        margin-bottom: 4rem;
    }

    .project-container img {
        height: 330px;
    }
}

@media screen and (max-width: 770px) {
    .projects {
        height: 280vh;
    }

    .projects-content {
        height: 100%;
    }
}

@media screen and (max-width: 540px) {
    .projects {
